.dayuan-menu {
  font-family: 'Inter var', sans-serif;
  position: absolute;
  /* top: 0;*/
  left: 0; 
  right: 0;
  bottom: 0px;
  width: 100%;
  /* height: 100%; */
  display: flex;
  color: #808080;
  padding: 20px;
  pointer-events: none;
  justify-content: flex-start;
  align-items: flex-end;
  flex-direction: row;
  font-size: 10px;
  line-height: 1.5em;
}

.dayuan-menu > div {
  word-wrap: none;
  word-break: none;
  white-space: pre;
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.dayuan-menu > div b {
  font-weight: 600;
  color: #b0b0b0;
}

.dayuan-menu a {
  pointer-events: all;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

.dayuan-menu a:hover {
  text-decoration: underline;
  color: inherit;
}


