@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.container {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  z-index: 1000;
}

.bg {
  background: linear-gradient(to top, #0a0a0a, #181a1a, #1d1d1f);
} 

.clicked {
  pointer-events: none;
  opacity: 0;
}

h1 {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0) rotate3d(1, 0, 0, 55deg) rotate3d(0, 0, 1, 45deg);
          transform: translate3d(-50%, -50%, 0) rotate3d(1, 0, 0, 55deg) rotate3d(0, 0, 1, 45deg);
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 800;
  margin: 0;
  margin-bottom: 0.5rem;
  font-size: 450px;
  letter-spacing: -40px;
  color: inherit;
}

.close {
  color: #000;
}

.close:hover {
  color: #e63946;
}



.UI {
position: absolute;
top: 50%;
left: 50%;
z-index: 100;
}

canvas {
  opacity: 0;
  touch-action: none;
  -webkit-animation: fade-in 1s ease 0.3s forwards;
          animation: fade-in 1s ease 0.3s forwards;
}

canvas:active {
  cursor: grabbing;
}




